import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { DirektoriHrNotesService, DirektoriPegawaiMasterService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Hr_notes, Pegawai_master } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private direktoriHrNotesService: DirektoriHrNotesService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    data: Hr_notes = {}
    id_hr_notes: string = ''
    toogleUpload = false

    ngOnInit() {
        this.id_hr_notes = this.activatedRoute.snapshot.paramMap.get('id')
        this.form()
        this.getPreeData()
        this.getData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'hrmanagement_notes.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }


    listPegawai: Pegawai_master [] = []
    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data => {
                this.listPegawai = data.data

            }
      )
    }

    getData() {
        this.spinner.show()
        this.direktoriHrNotesService.getDirektoriHrNotesId(this.id_hr_notes).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "hrmanagement_notes",
                    for_table: "erp_hr.hr_notes",
                    for_module_id: data.data.id_hr_notes
                })
            }
        )
    }


    dataForm: FormGroupTypeSafe<Hr_notes>
    form() {
        this.dataForm = this.fb.group<Hr_notes>(
            {
                id_pegawai: new FormControl(''),
                kategori: new FormControl('', [Validators.required]),
                tanggal: new FormControl(''),
                status: new FormControl(''),
                solusi: new FormControl(''),
                notes: new FormControl(''),
                id_dokumen: new FormControl(''),
                id_hr_notes: new FormControl(''),
                id_user: new FormControl(''),
                title: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
            }
        )
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.direktoriHrNotesService.putDirektoriHrNotesId({id: this.id_hr_notes, body: this.data}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/direktori/hr-management/notes/list'])
    }

}
