import { Component, OnInit } from '@angular/core';
import { DirektoriHrNotesService, DirektoriVPegawaiPosisiService, DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { Hr_notes, Pegawai, V_pegawai_posisi } from 'src/app/sdk/hr/models';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private direktoriHrNotesService: DirektoriHrNotesService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private router: Router,
        private spinner: NgxSpinnerService,
    ) { }

    id_hr_notes: string = ''

    dataNotes: Hr_notes = {}
    pegawai: Pegawai = {}
    pegawai_posisi: V_pegawai_posisi = {}


    ngOnInit() {
        this.id_hr_notes = this.routerActive.snapshot.paramMap.get('id')
        this.getPegawaiSp();

    }


    getPegawaiSp() {
        this.spinner.show()
        this.direktoriHrNotesService.getDirektoriHrNotesId(this.id_hr_notes).subscribe(
            response => {
                this.dataNotes = response.data
                this.getPegawai(response.data.id_pegawai)
                this.getPegawaiPosisi(response.data.id_pegawai)
                this.spinner.hide()

            }
        )
    }
    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            response => {
                this.pegawai = response.data
                this.spinner.hide()
            }
        )
    }

    getPegawaiPosisi(id_pegawai: string) {
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisisId(id_pegawai).subscribe(
            response => {
                this.pegawai_posisi = response.data
            }
        )
    }


    goToBack() {
        this.router.navigate(['/hr/direktori/hr-management/notes/list'])
    }

}
